import React, { useState, useEffect } from 'react'
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import '../css/HomeSlider.css'
import 'react-toastify/dist/ReactToastify.css';
import baseUrl from '../../helper/helper';
import HomeCategory from './HomeCategory';
function UserShoppingPage(props) {

    const responsive = {
        superLargeDesktop: {
            // the naming can be any, depends on you.
            breakpoint: { max: 4000, min: 3000 },
            items: 5
        },
        desktop: {
            breakpoint: { max: 3000, min: 1024 },
            items: 4
        },
        tablet: {
            breakpoint: { max: 1024, min: 464 },
            items: 2
        },
        mobile: {
            breakpoint: { max: 464, min: 0 },
            items: 1
        }
    };
    const [data, setData] = useState([]);
    // const listData = []

    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async () => {
        try {
            const response = await fetch(`${baseUrl}/product/category/Equipments`);
            // const response = await fetch("http://localhost:5005/product/category/Equipments");
            
            const jsonData = await response.json();
            // listData = jsonData;
            setData(jsonData);
        } catch (error) {
            console.log('Error:', error);   
        }
    };
    console.log(data)
    // console.log("ListData : "+listData)
    

    return (
        <div className='carasouldiv'>
            {/* <button onClick={handleFormSubmit}>Show Data</button> */}
            <HomeCategory/>   
            <h1>Eqiments</h1>
            <Carousel autoPlaySpeed={4000} responsive={responsive}>

                {data.map(item => (
                    <div className="newcard">
                        <img
                            className="product--image"
                            src={item.imagePath}
                            alt=""
                        />
                        <h5>{item.productName}</h5>
                        {/* <h5>{item.productDescription}</h5> */}
                        <p className='fakeprice'>Rs{item.individualProductPrice}</p>
                        
                        <p className="price">Rs {item.natePriceWithDiscount}</p>
                        
                        <p className='smallp'>*Effective Price</p>
                        <p className="smallp" style={{paddingLeft:70,color:"green"}}>{item.discountPercentage}% off</p>
                        <button>ADD TO CART</button>
                    </div>
                ))}
            </Carousel>

        </div>
    )
}

export default UserShoppingPage