import React, {useState, useEffect} from 'react'
import '../css/AddVendors.css'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useNavigate } from 'react-router-dom';
import baseUrl from '../../helper/helper';

function AddProducts() {
    const phoneNumberRegex = /[6-9][0-9]{9}/;
    const [formData, setFormData] = useState({
        image : File,
        productName : "",
        productDescription: "",
        category: "",
        productQuantity: "",
        individualProductPrice: "",
        discountPercentage: "",
        vendorId: "",
        vendorName : "",
        vendorEmail : "",
        status: "",
        action: ""
    });



    const navigate = useNavigate();

    const createProducts = async (event) => {
        event.preventDefault();
        try {
            
           
            

                const response = await fetch(`${baseUrl}/product/`, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    params: formData
                });
                if (response.status == 503) {
                    toast('Server is down', {
                        position: 'top-center'
                    });
                } else if (response.status == 500) {
                    toast('Internal Server Error', {
                        position: 'top-center'
                    });
                }
                else if (response.status == 404) {
                    toast('Method Error', {
                        position: 'top-center'
                    });
                }
                else if (response.ok) {

                    const jsonData = await response.json();


                    console.log(jsonData);
                    toast('Products has been Created')


                } else {

                    throw new Error('Failed to Create');
                }
            
        } catch (err) {
            // Handle error
            toast('Server is down', {
                position: 'top-center'
            });
            console.error(err);
        }
    }


    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setFormData(prevFormData => ({
            ...prevFormData,
            [name]: value
        }));
    }
    return (
        <>
            <ToastContainer />
            <h2>Create Product</h2>
            <div className='add'>
                <form className='container'>
                    <div class="mb-3">
                        <input type="file" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='image' onChange={handleInputChange} value={formData.image} name={formData.image} required />
                    </div>
                    <div class="mb-3">
                        <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Email Id' onChange={handleInputChange} value={formData.productName} name={formData.productName} required />
                    </div>  
                    <div class="mb-3">
                        <input type="password" class="form-control" id="exampleInputPassword1" placeholder='Password' onChange={handleInputChange} value={formData.productDescription} name={formData.productDescription} required />
                    </div>
                    <div class="mb-3">
                        <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Category' onChange={handleInputChange} value={formData.category} name='firstName' required />
                    </div>
                    <div class="mb-3">
                        <input type="text" class="form-control" id="exampleInputPassword1" placeholder='Product Quantity' onChange={handleInputChange}  value={formData.productQuantity} name='lastName' required />
                    </div>
                    <div class="mb-3">
                        <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Individual Product Price' onChange={handleInputChange} value={formData.individualProductPrice} name='phone' required />
                    </div>
                    <div class="mb-3">
                        <input type="text" class="form-control" id="exampleInputPassword1" placeholder='Discount' onChange={handleInputChange} value={formData.discountPercentage} name={formData.discountPercentage} required />
                    </div>
                    
                    
                    <div>
                        <button type="submit" class="form-label  btn2" onClick={createProducts}>CREATE</button>
                    </div>
                </form>
            </div>
        </>
    )
}

export default AddProducts