import React, { useState, useEffect } from 'react'
import '../css/DashboardGraph.css'
import baseUrl from '../../helper/helper';
import { Link } from 'react-router-dom'
function Prcards() {
    const [product, setProduct] = useState([]);
    // const listproduct = []

    useEffect(() => {
        fetchProduct();
    }, []);

    const fetchProduct = async () => {
        try {
            const response = await fetch(`${baseUrl}/product/`);


            const jsonproduct = await response.json();
            // listproduct = jsonproduct;
            setProduct(jsonproduct);
        } catch (error) {
            console.log('Error:', error);
        }
    };
    console.log(product)
    var date;
    return (
        <div>
            <>
                <div className='container row'>
                    <div class="orcard1 col">
                        <div class="card-body">
                            <h4 class="card-title">Recent Products</h4>
                            <p>+3.34%</p>
                            <h5>108</h5>
                        </div>
                    </div>
                    <div class="orcard2 col">
                        <div class="card-body">
                            <h4 class="card-title">Listed Products</h4>
                            <p>+2.34%</p>
                            <h5>34</h5>
                        </div>
                    </div>
                    <div class="orcard2 col">
                        <div class="card-body">
                            <h4 class="card-title">Unlisted Products</h4>
                            <p>+3.34%</p>
                            <h5>22</h5>
                        </div>
                    </div>
                    <Link to={'/addproduct'} class="addproduct col">
                        <div>
                            <div class="card-body">
                                <i class="ri-add-line"></i>
                                <h4 class="card-title"> Add Product</h4>
                            </div>
                        </div>
                    </Link>
                </div>
                <div className='longdiv1'>
                    <h5>All Products</h5>
                    <nav>
                        <li>All</li>
                        <li>Today</li>
                        <li className='a1'>Weekly</li>
                        <li>Monthly</li>
                    </nav>
                </div>
                <div class="col-md-12 container table-responsive">

                    <table WIDTH="1300" HEIGHT="50">
                        <thead>
                            <tr>
                                <th>No. </th>
                                <th>Product Id</th>
                                <th>Date</th>
                                <th>Vendor Name</th>
                                <th>Product Type</th>
                                <th>Product Name</th>
                                <th>Quantity</th>
                                <th>Price</th>
                                <th>Total Price</th>
                                <th>Status</th>
                                <th>Action</th>
                            </tr>
                        </thead>


                        <tbody>
                            {product.map((item, index) => (
                                <tr>
                                    <td>{index + 1}</td>
                                    <td>{item.productId}</td>
                                    <td>{date = new Date(item.date).toDateString()}</td>
                                    <td>{item.vendorName}</td>
                                    <td>{item.category}</td>
                                    <td>{item.productName}</td>
                                    <td>{item.productQuantity}</td>
                                    <td>{item.natePriceWithDiscount}</td>
                                    <td>{item.totalProductPrice}</td>
                                    <td>{item.status}</td>
                                    <td>{item.action}</td>

                                </tr>

                            ))}
                        </tbody>
                    </table>
                </div>

                {/* <div className='largediv1'>
                    <nav className='navdiv'>
                        <li>No.</li>
                        <li>Product ID</li>
                        <li>Date</li>
                        <li>Vendor Name</li>
                        <li>Product Type</li>
                        <li>Category</li>
                        <li>Quantity</li>
                        <li>Price</li>
                        <li>Total Price</li>
                        <li>Status</li>
                        <li>Action</li>
                    </nav>
                </div> */}
            </>
        </div>
    )
}

export default Prcards
